.page-404 {
	margin-top: 10vh;
	font-family: "Mont";
	&__code {
		display: flex;
		align-items: flex-end;
		font-size: 200px;
		font-weight: 800;
		line-height: 1;
		color: $blue;

		filter: drop-shadow(0 0 80px rgba(white, 0.3));

		img {
			max-width: 100%; }

		@include media(767px) {
			justify-content: center;
			font-size: 160px; } }

	&__desc {
		font-size: 27px;
		font-weight: 800;
		transform: translateY(-100%);
		white-space: nowrap;

		@include media(767px) {
			text-align: center;
			font-size: 22px; } }

	&__text {
		margin-top: 75px;
		max-width: 446px;
		font-size: 20px;
		font-weight: 300;

		@include media(767px) {
			margin: {
				left: auto;
				right: auto; }
			text-align: center; } }

	&__btn-group {
		display: flex;
		margin-top: 96px;

		@include media(767px) {
			justify-content: center;
			flex-wrap: wrap; } }

	&__button {
		min-width: 60px;
		margin-right: 20px;

		&:last-child {
			margin-right: 0; }

		@include media(1199px) {
			font-size: 14px;
			padding-left: 15px;
			padding-right: 15px; }
		@include media(767px) {
			flex-grow: 1;
			margin: 0 10px;
			margin-bottom: 20px;
			&:last-child {
				margin-right: 10px; } } } }
