.bb-floating {
	position: fixed;
	bottom: 93px;
	right: 93px;
	z-index: 50;

	@include media(575px) {
		bottom: 20px;
		right: 50%;
		transform: translateX(50%); }

	&__button {
		display: block;
		width: 80px;
		height: 80px;
		border: none;

		padding: 0;

		border-radius: 50%;

		color: #fff;
		text-decoration: none;

		&:focus {
			outline: none;

			span {
				box-shadow: 0 0 0 1px #fff, 0 0 15px 2px $blue; } }

		&.active,
		&:hover {
			span {
				background-color: $darkblue;
				box-shadow: 0 0 0 2px #fff; } }

		span {
			position: relative;

			display: inline-flex;
			justify-content: center;
			align-items: center;
			width: 80px;
			height: 80px;
			padding: {
				top: 0;
				bottom: 0;
				left: 10px;
				right: 10px; }

			box-shadow: 0 0 6px rgba(14, 61, 94, 0.2);
			border-radius: 50%;
			background-color: #64b2db;
			box-sizing: border-box;
			text-align: center;

			transition-property: background-color, box-shadow;
			transition-duration: 0.3s; }


		&::after {
			@extend %bluebox-rings;
			width: 140px;
			height: 140px;
			animation-delay: 0.5s; }

		&::before {
			@extend %bluebox-rings;
			width: 164px;
			height: 164px; }

		@include media(575px) {
			box-shadow: 0 0 15px rgba(14, 61, 94, 0.31);

			&::before, &::after {
				display: none; } } }

	&__items {
		position: absolute;
		right: 0;
		top: 0;
		z-index: 2;

		display: flex;
		justify-content: center;
		align-items: center;
		width: 22px;
		height: 22px;
		border: 1px solid #ffffff;

		font-size: 12px;

		border-radius: 50%;
		background-color: #0e3d5e;
		color: #fff; }

	&__modal {
		position: absolute;
		right: -41px;
		bottom: 100%;

		width: 365px;
		max-width: calc(100vw - 20px);
		margin-bottom: 33px;

		font-size: 18px;

		background-color: #ffffff;
		color: $darkblue;

		transform: translateY(20px);
		transition: transform 0.3s, opacity 0.3s;
		opacity: 0;
		pointer-events: none;
		filter: drop-shadow(0 0 18px rgba(6, 27, 41, 0.1));

		&::before {
			content: '';

			position: absolute;
			right: 70px;
			top: 100%;

			width: 20px;
			height: 20px;
			background-color: #fff;

			transform: translateY(-50%) rotate(-45deg); }

		@include media(575px) {
			right: 50%;
			transform: translate(50%, 20px);

			&::before {
				right: 50%;
				transform: translate(50%, -50%) rotate(-45deg); } }

		&.visible {
			transform: translateY(0);
			opacity: 1;
			pointer-events: auto;

			@include media(575px) {
				transform: translate(50%, 0); } }

		&.show-details {
			width: 415px;

			.bb-floating__box {
				display: none; }

			.bb-floating__detail {
				display: block; } } }

	&__close {
		@extend .modal__close; }

	&__title {
		@extend .cart-modal__header;
		font-size: 20px;
		padding: {
			top: 18px;
			bottom: 18px; } }

	&__list {
		padding: 20px 20px 0;

		.cart-sm-product__image {
			flex-basis: 85px;
			max-width: 85px; }

		@include media(575px) {
			padding: 10px 10px 0;

			.cart-sm-product__image {
				flex-basis: 60px;
				max-width: 60px; }

			.cart-sm-product__price {
				font-size: 14px; } } }

	&__box {

		.cart-sm-product {
			justify-content: space-between;
			padding-left: 0;
			padding-right: 0;
			padding-top: 0; }

		.cart-sm-product__price {
			margin-left: 0; } }

	&__detail {
		display: none;
		padding-bottom: 15px;
		border-bottom: 1px solid #dfe7eb;

		@include media(575px) {
			padding-bottom: 5px; }

		.cart-sm-product {
			margin-bottom: 5px; }

		.cart-sm-product__title {
			flex-basis: 82px;
			flex-shrink: 0;
			max-width: 82px;

			@include media(575px) {
				font-size: 12px;
				max-width: 60px; } }

		.cart-sm-product__items {
			@include media(575px) {
				max-width: 60px; } } }

	&__checkout {
		padding-top: 20px;
		padding-bottom: 15px;
		padding-left: 20px;
		padding-right: 20px;
		text-align: right;

		button {
			padding: {
				top: 8px;
				bottom: 9px; }
			font-size: 14px; } } }


%bluebox-rings {
	content: '';

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: -2;

	transform: translate(-50%, -50%);

	background-color: #fff;
	border-radius: 50%;

	animation-name: pulsing;
	animation-duration: 3s;
	animation-timing-function: linear;
	animation-iteration-count: infinite; }

@keyframes pulsing {
	0% {
		transform: translate(-50%, -50%) scale(0);
		opacity: 0.8; }
	30% {
		transform: translate(-50%, -50%) scale(0.5); }
	50% {
		opacity: 0.8; }
	100% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 0; } }
