.cart-modal {
	position: absolute;
	right: 40px;
	top: 100px;
	z-index: 100;

	display: flex;
	flex-direction: column;
	width: 365px;
	max-width: calc(100vw - 10px);
	min-height: 80vh;
	margin-top: 2em;

	box-shadow: 0 0 18px rgba(6, 27, 41, 0.1);
	background-color: #ffffff;
	color: $darkblue;

	transform: translateY(-20px);
	transition: transform 0.3s, opacity 0.3s;
	opacity: 0;
	pointer-events: none;

	font-size: 18px;

	&.visible {
		transform: translateY(0);
		opacity: 1;
		pointer-events: auto; }

	@include media(1699px) {
		top: 70px; }

	@include media(1199px) {
		right: 50px; }

	@include media(767px) {
		right: 16px; }

	@include media(399px) {
		right: 50%;
		transform: translate(50%, -20px);

		&.visible {
			transform: translate(50%, 0); } }

	&__close {
		@extend .modal__close;

		top: 27px; }

	&__header {
		width: 100%;
		padding: 22px 20px;
		border-bottom: 1px solid #dfe7eb;

		font-family: "Mont";
		font-size: 26px;
		font-weight: 800;
		letter-spacing: 0.6px;

		box-sizing: border-box;

		color: $blue; }

	&__list {
		flex: 1 1 100%;
		overflow-y: auto; }

	&__price {
		// display: none
		padding-top: 18px;

		&.visible {
			display: block; } }

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 20px; }

	&__label {
		font-size: 16px;
		letter-spacing: 0.89px;

		color: rgba($blue, 0.3); }

	&__total {
		color: $blue;
		font-size: 24px;
		letter-spacing: 0.76px;
		text-transform: uppercase; }

	&__buttons {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 0;
		margin-top: auto;
		padding-bottom: 45px; }

	&__back {
		margin-top: 17px;
		color: $blue;
		font-size: 16px;
		letter-spacing: 0.89px;
		text-transform: uppercase; } }


.cart-sm-product {
	display: flex;
	align-items: center;
	padding: 15px 20px;
	border-bottom: 1px solid #dfe7eb;

	&_bb {
		border: 1px solid #e2e7ea;
		padding: 5px 10px; }

	&__image {
		flex: 0 0 128px;
		max-width: 128px;
		padding-right: 10px;

		box-sizing: border-box;

		img {
			max-width: 100%; }

		@include media(399px) {
			flex-basis: 80px;
			max-width: 80px; } }

	&__title {
		font-size: 16px;
		font-weight: bold;
		letter-spacing: -0.2px;
		line-height: 19px; }

	&__price {
		margin-right: 0;
		margin-left: auto;
		padding-left: 10px;

		font-size: 18px;
		font-weight: bold;
		text-transform: uppercase;
		white-space: nowrap;
		text-align: right;
		color: $blue; }

	&__items {
		position: relative;

		display: flex;
		align-items: center;
		justify-content: center;
		flex-basis: 70px;
		flex-shrink: 0;
		max-width: 70px;

		input {
			position: absolute;
			top: 0;
			left: 0;

			width: 1px;
			height: 1px;
			padding: 0;
			border: none;

			background: none;
			overflow: hidden;
			appearance: none;

			&:focus {
				outline: none; } }

		span {
			width: 22px;
			text-align: center; } }

	&__num-control {
		padding: 0;
		border: none;

		background: none;
		overflow: hidden;
		appearance: none;

		&:focus {
			outline: none; } }

	&__view-details {
		padding: 3px;
		border: none;

		font-size: 12px;
		letter-spacing: 0.28px;

		appearance: none;
		background: none;
		text-decoration: underline;

		&:focus {
			outline: none;
			cursor: pointer; } } }
