.auth-user {
	position: relative;
	font-size: 16px;

	&__button {
		margin-left: 2.5vw;
		padding: 12px 20px;

		font-size: 16px;
		font-weight: 600;

		border-radius: 30px;
		border: none;
		appearance: none;
		background: rgba($blue, 0.1);
		color: $blue;
		cursor: pointer;
		white-space: nowrap;

		transition: box-shadow 0.2s, background 0.3s;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px $blue; }

		&:active {
			box-shadow: 0 0 2px 1px $blue; }

		&:hover {
			background: rgba($blue, 0.3); }

		@include media(767px) {
			margin-left: 0;
			margin-top: 1em; } }

	&__icon {
		width: 14px;
		height: 16px;

		margin-right: 1em;
		vertical-align: baseline;

		@include media(1199px) {
			margin-right: 0; } }

	&__arrow {
		width: 12px;
		height: 7px;

		margin-left: 6px;
		vertical-align: middle;

		@include media(767px) {
			transform: rotate(180deg) translateY(2px); } }

	&__btn-text {
		@include media(1199px) {
			display: none; } }

	&__modal {
		position: absolute;
		top: 100%;
		right: 0;
		z-index: 10;

		width: 415px;
		max-width: calc(100vw - 20px);
		margin-top: 30px;

		box-shadow: 0 0 16px 14px rgba(232, 237, 240, 0.18);
		background-color: #ffffff;
		filter: drop-shadow(0 0 18px rgba(6, 27, 41, 0.1));

		transform: translateY(-20px);
		opacity: 0;
		pointer-events: none;
		transition: transform 0.3s, opacity 0.3s;

		&::before {
			content: '';

			position: absolute;
			right: 75px;
			top: 0;

			width: 20px;
			height: 20px;
			background-color: #fff;

			transform: translateY(-50%) rotate(-45deg);

			@include media(1199px) {
				right: 30px; } }

		@include media(767px) {
			top: auto;
			bottom: 100%;
			right: 50%;
			margin-top: 0;
			margin-bottom: 30px;
			transform: translate(50%, -20px);

			&::before {
				top: 100%;
				right: 50%;
				transform: translate(50%, -50%) rotate(-45deg); } }



		&.visible {
			opacity: 1;
			pointer-events: auto;
			transform: translateY(0);

			@include media(767px) {
				transform: translate(50%, 0); } } }
	&__row {
		display: flex; }

	&__col {
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex-basis: 50%;
		max-width: 50%;
		padding: {
			left: 20px;
			right: 20px;
			top: 35px;
			bottom: 40px; }

		box-sizing: border-box;

		color: #0e3d5e;

		&:nth-child(1) {
			border-right: 1px solid #e1e8ec; }

		@include media(575px) {
			padding: {
				left: 10px;
				right: 10px; } }

		&_main {
			text-align: center;

			button {
				min-width: 0;
				margin-top: 30px;
				padding: {
					top: 10px;
					bottom: 10px; }

				@include media(429px) {
					padding: {
						left: 15px;
						right: 15px; } } } } }

	&__s-header {
		margin-top: 17px;
		font-size: 13px;

		color: #cfd8df; }

	&__plan {
		font-size: 18px;
		line-height: 30px; }

	&__name {
		font-family: "Mont";
		font-size: 20px;
		font-weight: 800;

		color: $blue; }

	&__links {
		font-size: 14px;
		font-weight: 300;
		line-height: 28px;

		a {
			color: inherit;
			text-decoration: none;

			&:hover {
				color: $blue; } } }

	&__logout {
		margin-top: 10px;
		font-weight: 600;
		color: $blue; } }
